.nav-wrapper {
  /* border: 1px solid red; */
  height: 10vh;
  display: flex;
  align-items: center;
  background-color: #222222;
  position: fixed;
  top: 0;
  width: 100%;
}
.nav-flex {
  display: flex;
  max-width: 80%;
  justify-content: space-between;
  /* border: 1px solid red; */
  margin: 0 auto;
  width: 100%;
  align-items: center;
  /* align-items: center; */
}
.nav-left {
  font-family: "Koulen", cursive;
  /* color: #6e6e6e; */
}
.nav-center {
  display: flex;
  /* border: 1px solid blue; */
}
.nav-flex,
.nav-center > li {
  font-size: 18px;
  line-height: 10px;
  padding: 15px 30px !important;
  /* font-weight: 400; */
  color: white;
  margin: auto;
}
.nav-center > li > a {
  color: white;
  font-family: "Koulen", cursive;
  /* color: #6e6e6e; */
}
.nav-right > button {
  padding: 10px 20px;
  font-size: 17px;
  border-radius: 4px;
  /* border: 2px solid #68d372; */
  color: white;
  background-color: #68d372;
  width: max-content;
  text-transform: none;
  font-weight: 400;
  cursor: pointer;
}

@media only screen and (max-width: 1400px) {
  .nav-center{
    display: none;
  }
  
}
